nav {
    background: rgba(0, 0, 0, 0.3);
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    gap: 2rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);
}

nav a {
    background: transparent;
    padding: 1.2rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    font-size: 1.1rem;
}

nav a:hover {
    background: rgba(0, 0, 0, 0.3);
}

nav a.active {
    background: var(--color-prim);
    color: var(--color-bg);
}

/* ============================ Media Queries (Medium Devices) ============================ */
@media screen and (max-width: 1024px) {
    nav {
        gap: 1rem
    }

    nav a {
        padding: 1.1rem;
        font-size: 1.1rem;
    }
}

/* ============================ Media Queries (Small Devices) ============================ */
@media screen and (max-width: 600px) {
    nav {
        gap: 1rem;
    }

    nav a {
        padding: 1.1rem;
        font-size: 1.1rem;
    }
}